const Button = ({ label, color, currentCashflow, onBtnClick }) => {
  console.log({ label, color, currentCashflow });

  const active =
    currentCashflow.toLowerCase() === label.toLowerCase()
      ? `text-white bg-${color}-600 hover:bg-${color}-700 `
      : `text-${color}-600 bg-transparent hover:bg-${color}-200`;

  console.log({ active });

  return (
    <button
      onClick={onBtnClick}
      className={` ${active} ring-2 ring-offset-2 ring-${color}-600  py-1 px-5 outline-none font-semibold rounded transition-colors`}
    >
      {label}
    </button>
  );
};

export default Button;
